.App {
  width: 450px;
}

.AppIFrame {
  width: 450px;
  height: 420px;
  background-color: transparent;
}

.AppLoader {
  width: 450px;
  height: 420px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.title-container {
  display: flex;
  flex-direction: row;
  height: 58px;
}

.title {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.title-row {
  display: flex;
  flex-direction: row;
}

.title-icon {
  margin-left: 15px;
  margin-top: 15px;
  width: 28px;
  height: 28px;
}

.title-text {
  font-size: 24px;
  font-family: Roboto-bold, Roboto, Arial, sans-serif;
  color: rgba(0, 0, 0, 0.75);
  font-weight: bold;
  margin-top: 15px;
  margin-left: 12px;
}

.header-icon {
  width: 20px;
  height: 20px;
  margin-top: 8px;
}

.button {
  cursor: pointer;
  max-width: 100%;
  text-align: center;
  height: 36px;
  padding-top: 10px;
  padding-bottom: 0px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 6px;
  border-width: 0px;
  font-family: Roboto, Arial, sans-serif;
  letter-spacing: normal;
  font-size: 14px;
  line-height: 36px;
  text-align: center;
}

.flat-button {
  color: rgb(28, 30, 33);
  font-weight: normal;
  border: none;
  background-color: white;
}
